* {
  box-sizing: border-box;
}

.box {
  background-color: #0ff;
  border-radius: 25px;
  width: 300px;
  height: 300px;
  margin-top: 15px;
  transition: all .3s;
}

.field {
  border: 5px solid #000;
  border-radius: 15px;
  width: 700px;
  height: 100px;
  margin-top: 30px;
  padding: 10px;
  position: relative;
}

.ball {
  background-color: red;
  border-radius: 50%;
  width: 70px;
  height: 70px;
  transition: all 10ms;
  position: absolute;
}

.game {
  border: 5px solid #000;
  border-radius: 15px;
  width: 700px;
  height: 300px;
  margin-top: 30px;
  padding: 10px;
  position: relative;
}

.point {
  background-color: red;
  border-radius: 50%;
  width: 30px;
  height: 30px;
  transition: all .1s;
  position: absolute;
  top: 10px;
  left: 10px;
}
